
import './index.scss'
import { Select,Input,Button,message } from 'antd';
const { Option } = Select;
const { TextArea } = Input;
import {connect} from 'react-redux'
import {getqt, getquestion} from '../../actions'
import {axios} from '&'
var style={
    first:{
        fontWeight:'bold',
        marginBottom:7
    },
    second:{
        marginBottom:20
    }
}
@connect(
    (state)=>{
        return{
            qt:state.getIn(['data','qt']),
        }
    }
)
export default class Question extends Component{
    constructor(){
        super()
        this.state={
            one:'请选择面试题目类别',
            question:[]
        }
    }
    onChange=(value)=>{
        this.setState({
            one:value
        })
        axios.get('/react/getquestion',{params:{q_type:value}}).then(res=>{
            this.setState({
                question:res.data.result
            })
        })
    }
    componentDidMount(){
        this.props.dispatch(getqt ({url:'/react/getqt'}))
        axios.get('/react/getquestion',{params:{q_type:'vue1'}}).then(res=>{
            this.setState({
                question:res.data.result
            })
        })
    }
    render(){
        var {
            one,question
        }=this.state
        return(
            <div> 
                <h2>面试答案</h2>
                <div style={style.second}>
                    <div style={style.first}>请选择面试题目类别：</div>
                    <Select style={{ width: 500 }}  onChange={this.onChange} value={one}>
                        {
                            this.props.qt.map((item,i)=>{
                                return (
                                    <Option value={item.q_type} key={i}>{item.value}</Option>
                                )
                            })
                        }
                    </Select>
                </div>
                <h2>面试答案:</h2>
                {
                    question.map((item,i)=>{
                        return(
                            <div style={style.second} key={i}>
                                <div style={style.first}>{item.wenti}</div>
                                <TextArea
                                    value={item.answer}
                                    style={{ width: 500 }}
                                    autosize={{ minRows: 7, maxRows: 8}}
                                />
                            </div>
                        )
                    })
                }
            </div>
        )
    }
}